<template>
  <div class="home">
    <Menu mode="horizontal" theme="primary">
      <MenuItem>
        <Icon type="navicon"></Icon>
        DeepSearch Web Demo
      </MenuItem>
    </Menu>
    <br>
    <Row>
      <i-col span="13" push="4">
        <Input v-model="query_string" placeholder="Search Something..." />
      </i-col>
      <i-col span="4" push="4">&nbsp;
        <Button type="primary" icon="ios-search" @click="searchAjax">Search</Button>
      </i-col>
    </Row>
    <br>
    <Row v-for="hit in hits">
      <i-col span="13" push="4">
        <Card>
          <p slot="title" v-html="getTitle(hit.highlight.title, hit._source.title)"></p>
          <Poptip trigger="hover" :title="hit._source._id" width="400" placement="right">
            <div slot="content">
              <div style="width: 350px;height: auto;max-height: 300px;">
                <span style="white-space: pre-line" v-html="hit._source.abstr"></span>
              </div>
            </div>
            <p v-html="getSnippet(hit.highlight.abstr, hit._source.abstr)"></p>
          </Poptip>

        </Card>
        <br>
      </i-col>
      <i-col span="4" push="5">
      </i-col>
    </Row>
    <br>

    <Row>
      <i-col span="13" push="4">
        <Page push="2" @on-change="onPageChange" :current="current" :total="total" show-total show-elevator></Page>
      </i-col>
    </Row>
    <br>

  </div>
</template>
<script>
  import $ from "jquery";
  import router from '../router'

  export default {

    name: 'Home',
    data () {
      return {
        query_string: '',
        hits: [],
        total: 0,
        current: 1,
        searches: [],
      }
    },
    methods: {
      onPageChange(page) {
        console.log(page);
        this.current = page;
        this.searchAjax();
      },

      getTitle(highlight, title) {
        if (highlight !== undefined) {
          if (highlight.length === 1) {
            title = highlight[0];
          }
        }
        title = "<span>" + title + "</span>";
        return title
      },
      getSnippet (highlight, content) {
        if(highlight !== undefined) {
          let result = "";
          highlight.forEach((line)=>{
            result += line + "<br/><br/>"
          });
          result.trim("<br/>");
          return result;
        }
        if(content.length > 300) {
          return content.substring(0, 200)+".....";
        }
        return content
      },
      getContent(value) {
        String.prototype.replaceAll = function(s1,s2){
          return this.replace(new RegExp(s1,"gm"),s2);
        };
        return value
      },

      searchAjax() {
        this.hits = [];
        let that = this;
        this.$Spin.show();
        $.ajax({
          url: "/api/search",
          data: {
            "page": this.current,
            "query": this.query_string
          },
          type: "GET",
          dataType: "json",
          success (json) {
            that.$Spin.hide();
            that.hits = json.hits.hits;
            console.log(that.hits);
            that.total = json.hits.total;
            console.log(that.total);
          },
        });
      },

    }
  }

</script>
<style>
  em{
    color: red;
    font-style: normal;
  }
</style>



